#myCarousel .list-inline {
  white-space: nowrap;
  overflow-x: visible;
}

#myCarousel .carousel-indicators {
  position: static;
  left: initial;
  width: initial;
  margin-left: initial;
}

#myCarousel .carousel-indicators > li {
  width: initial;
  height: initial;
  text-indent: initial;

  & img {
    width: 148px;
    height: 124px;
    border-radius: 10px;
    transition: transform 250ms linear;

    @media screen and (max-width: 1439px) {
      width: 222px;
      height: 186px;
    }

    @media screen and (max-width: 719px) {
      width: 96px;
      height: 78px;
    }
  }
}
.img-fluid {
  max-width: 492px;
  width: 492px;
  height: 360px;
  object-fit: cover;
  border-radius: 10px;

  @media screen and (max-width: 1439px) {
    width: 679px;
    max-width: 679px;
    height: 497px;
  }

  @media screen and (max-width: 719px) {
    max-width: 317px;
    width: 317px;
    height: 232px;
  }
}

.img-slider {
  width: 148px;
  height: 124px;

  object-fit: contain;
}

.carousel-indicators {
  justify-content: space-between;
  margin-top: 24px;
}

.carousel-indicators li {
  margin: 0;
  border: none;
}

.carousel-indicators > li.active img {
  border-radius: 10px;
  // border: 10px solid black;
  box-shadow: 0px 0px 0px 11px #3563e9;
  outline: 9px solid #ffffff;
  transform: scale(0.87);
}

.carousel-indicators li {
  opacity: 1;
}
